export default class HomeService {
    constructor($http, $q) {
        this._$http = $http;
        this._$q = $q;
    }

    getData() {
        let request = {
            method: 'GET',
            url: 'https://jsonplaceholder.typicode.com/todos/1'
        };
        return this._$http(request).then((res) => res.data);
    }
}

HomeService.$inject = ['$http', '$q'];


